@tailwind base;
@tailwind components;
@tailwind utilities;
.Course {
  border-radius: 10px;
  margin-bottom: 1rem;
  transition: all 0.5s;
  width: 250px;
}
.Course:hover {
  transform: translateY(-10px);
}
.whyonlyus{
  position: relative;
}

.rectengle::before{
height: 100%;
width: 850px;
position: absolute;
top: 0;
left:-400px ;
background-image: url("../../assets/images/rectengelbefore.svg");
background-size: cover;
content: "";

}
.rectengle::after{
  height: 60%;
  width: 450px;
  position: absolute;
  right: 0;
  top: 25%;
  
  content: "";
  background-size: cover;
  background-image: url("../../assets/images/circle.svg");

}
.Ourstracturebefore::before{
  content: "";
  position: absolute;
  width: 100%;
  background-image: url('../../assets/images/Ourstracturebefore.svg');
  height: 507px;
  background-repeat: no-repeat;
  left: 0;
}
.Ourstracturebefore > *{
  /* position: relative; */
  z-index: 99;
}
.whyonlyus::before{
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  background-image: url('../../assets/images/WhyonlyUs.svg');
  background-repeat: no-repeat;
  top: 40% ;
  left: 10px;
  z-index: -10;
}
.home {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  /* position: absolute; */

  z-index: -10;
  /* height: 100vh; */
}
.home::after{

  /* height: 1191px; */
  background-position: center;
  top: -110px;
  z-index: -10;
  width: 100%;
  background-image: url('../../assets/images/bg.png');
  background-repeat: repeat;
  position: absolute;
  content: '';

}
.heading_1 {
  color: #164d77;
}

