  content: '';
  position: absolute;
  background-image: url('../../assets/images/before_1.png');
  width: 410px;
  height: 600PX;
  background-repeat: no-repeat;
  left: -250px;
  background-position: left;
  z-index: -3;
  top: 2%;
}
