.container {
  /* height: 100vh */
  max-height: fit-content;
  width: 100%;
  max-width: 1440px;
  margin: auto;
  padding: 0px 30px;
  box-sizing: border-box;
}
.container_home{
  height: fit-content;
}
.bannerimg {
  filter: drop-shadow(0 40px 10px rgba(0, 0, 0, 0.3));
  margin: 0;
  animation: bannerimg 1s infinite ease-in-out alternate;
}
@keyframes bannerimg {
  to {
    transform: translateY(-10px);
  }
}
.container2 {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container2 video {
  border: 1px solid rgba(0, 0, 0, 0.18);
  width: 60%;
  border-radius: 5px;
  outline: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.175);
}
@media screen and (max-width: 800px) {
    .img.bannerimg{
      margin-top: 0px;
      text-align: center;
    }

}
@media screen and (max-width: 1648px) {
  .home::before {
    display: none;
    position: relative;
      }.rectengle::before{
        width: 0;
        height: 0;}
        .rectengle::after{
          width: 0;
          height: 0;
        }
 
}

